<script setup>

import { ref, watch, watchEffect, onMounted, onBeforeUnmount } from "vue";
import { Tooltip } from "bootstrap";
import { gsap } from "gsap";

const bsPlacement = "top";

const props = defineProps({
    stageCount: {
        type: Number,
        validator(value) {
            return value > 0
        },
        default: 1
    },

    startingAt: {
        type: Number,
        default: 0
    },

    stagePrefix: {
        type: String,
        default: "R"
    }
});

const emits = defineEmits([
    "onSetLevel",
    "onPlay",
    "onStep",
    "onStepEnd",
    "onShowFilter",
    "onZoom",
    "onZoomEnd"
]);

const isOpen = ref(false);

const activeStageIndex = ref(props.startingAt);

const stages = ref([]);

let tooltipObj = undefined;

watch(() => props.startingAt, () => {
    activeStageIndex.value = props.startingAt;
});

watchEffect(() => {
    const count = props.stageCount;
    for (let i = 0; i < count; i++) {
        stages.value[i] = {
            index: i,
            title: props.stagePrefix.concat(i),
            isActive: activeStageIndex.value === i
        }
    }

    stages.value[0].title = "RDC";

    if (count > 1) {
        stages.value[count - 1].title = "Toit";
    }

});

function stageClick(index) {
    activeStageIndex.value = index;
    emits('onSetLevel', index);
}

const showBtn = ref(null);
const sidebar = ref(null);

function showMenu() {
    isOpen.value = true;
    gsap.set(sidebar.value, {
        autoAlpha: 0,
        y: "100%"
    });
    gsap.to(sidebar.value, {
        autoAlpha: 1,
        y: 0,
        duration: .3
    });
}

function closeMenu() {

    gsap.set(showBtn.value, {
        scale: 0,
        autoAlpha: 0,
    });

    gsap.to(sidebar.value, {
        autoAlpha: 0,
        y: "100%",
        duration: .3,
        onComplete: () => {
            isOpen.value = false
        }
    });

    gsap.to(showBtn.value, {
        scale: 1,
        autoAlpha: 1,
        duration: .3,
        delay: .3,
        ease: "back.out(3)",
    });
}



onMounted(() => {
    const tooltipTriggerList = document.querySelectorAll('[data-bs-toggle="tooltip"]');
    tooltipObj = [...tooltipTriggerList].map(tooltipTriggerEl => new Tooltip(tooltipTriggerEl, {
        trigger: "hover",
        customClass: "custom-tooltip",
    }));
});

onBeforeUnmount(() => {
    tooltipObj.forEach(tooltip => {
        tooltip.dispose();
    });
})

</script>




