const screenshot = ref(null);

const items = ref([
    {
        url: "",
        title: "Plan 3D",
        desc: ""
    },
    {
        url: "",
        title: "Plan 2d",
        desc: ""
    }
]);


const setImageSrc = () => {
    const apptName = currentRegion.value.nom;
    items.value[0].url = `assets/images/lots/3d/${apptName}.jpg`;
    items.value[1].url = `assets/images/lots/2d/${apptName}.jpg`;
};

function onEnter(el) {
    transitionEnter(el);
}

function onLeave(el, done) {
    transitionLeave(el, done);
}

defineExpose({
    setImageSrc
});

</script>



