import { Animation } from "./animation.js"
import { Hotspots } from "./hotspots.js";
import { TilesController } from "./tilesController.js";
import { InputSystem } from "./controls/input_system";
import { Resizer } from "./resizer";

/**
 * Animator handles drawing and animations interaction
 */

class Animator{

    /**
     * 
     * @param {Object} canvasGroup - all tools canvas 
     * @param {Array<String>} animationsPattern - array of animations path
     * @param {Object} options
     * @param {Array} hotspotRegions - hotspots regions , this is getting from the config file
     */
    constructor (canvasGroup , animationsPattern , options , hotspotRegions , regionInfo) {

        this.canvasGroup = canvasGroup;

        this.animationsPattern = animationsPattern;

        this._options = options,

        this.hotspots;
        
        this._tilesCanvas = canvasGroup.tilesCanvas;
        this._animCanvas = canvasGroup.animation;
        this._eventsCanvas = canvasGroup.eventsCanvas;

        this._hotspotRegions = hotspotRegions;

        this._regionInfo = regionInfo;

        this.init();
        
    }

    init() {

        this._currentWidth = 0;
        this._currentHeight = 0;

        this._countFrames = this._options.framesCount;
        this._tlTimeDraw = this._options.timeLineSpeed;

        this._animations = [];

        this.maxWidth = this._options.maxWidth;
        this.maxHeight = this._options.maxHeight;

        this._animationIndex = this._options.animStart;
        this._frameIndex = this._options.frameStart;
        
        this.mobileWidth = 500;
        this._reverse = false;
        this._isPlaying = false;
        this._currentFrame;
        this._animationsReady = false;
        this.cancel = false;

        this._tilesController = null;
        this._resizer = null;

        this.percentLoaded = 0;
        this._countAnimLoaded = 0;

        this._zoomPoints = undefined;

        this._animCtx = this._animCanvas.getContext("2d");
        this._tilesCtx = this._tilesCanvas.getContext("2d");

        this._loadAnimations();
    }
    
    /**
     * Load preloaded animations images
     * @private
     */
    _loadAnimations() {

        console.log("loading..");

        let loadPromises = [];

        const total = this.animationsPattern.length * this._countFrames;
        for (let i = 0; i < this.animationsPattern.length; i++) {
            const promise = new Promise( (resolve , reject) => {
                const pattern = this.animationsPattern[i];
                const anim = new Animation(pattern , this._countFrames , 
                (error) => {
                    if (this.cancel) reject("cancel");

                    if (typeof error !== "undefined") {
                        reject(error);
                    }else{
                        resolve();
                    }
                } , 
                () => {

                    this._countAnimLoaded++;
                    this.percentLoaded = Math.round( (this._countAnimLoaded / total) * 100 );
                    window.dispatchEvent(new CustomEvent("OnLoadingChange" , {
                        bubbles : false
                    }))
                });
                this._animations.push(anim);
            })

            loadPromises.push(promise);
        }

        Promise.all(loadPromises)
            .then( () => {
                console.log("all animations loaded!");
                this._animationsReady = true;
                this._setup();
            } )
            .catch( (e) => {
                if(e === "cancel") return;
                window.dispatchEvent(new CustomEvent("OnError", {
                    bubbles: false
                }));
                throw new Error('Error while loading animations! please check your images...');
            });
    }

    /**
     * setup the animator to be ready.
     * Once ready we dispatch the "OnAnimatorReady" custom event
     * @private
     */
    _setup() {

        //setting the starting animation and frame
        this._frameIndex = Math.min(Math.max(parseInt(this._frameIndex), 0), this._countFrames - 1);
        this._animationIndex = Math.min(Math.max(parseInt(this._animationIndex), 0), this._animations.length - 1);

        
        this._resizer = new Resizer(this.canvasGroup);
        this._initTiles();

        this.hotspots = new Hotspots(this.canvasGroup, this._hotspotRegions , this._regionInfo);

        this._inputSystem = new InputSystem(this._eventsCanvas , 
            {
                width : this.maxWidth , 
                height : this.maxHeight
            },
            this._options.zoomOptions,
            this._options.dragOptions,
        );
        

        this._inputSystem.rect.addEventListener('OnZoom' , () => {
            this._onZoomEvent();
        });

        this._inputSystem.rect.addEventListener('OnDrag' , (e) => {
            this.onDragEvent(e);
        });


        window.onresize = () => {
            this._resizer.resize();
            this._inputSystem.zoom.setMaxZoom();
            this.requestFrameDraw();
        };

        window.dispatchEvent(new CustomEvent("OnAnimatorReady", {
            bubbles: false
        }));
    }

    /**
     * instantiate tiles controller
     * @private
     */
    _initTiles() {
        this._tilesController = new TilesController(this._options);
    }

    /**
     * Run the Animator after the ready state
     */
    run() {
        this._showAnimationFrame(this._animationIndex , this._frameIndex);
        if(this._options.autoplay){
            window.setTimeout(() => this.play(), 0);
        } 
    }

    play() {
        if(this._isPlaying)
            return;

        window.requestAnimationFrame( (timestamp) => {this._update(timestamp)} );
        this._isPlaying = true;
    }

    stop() {
        if(!this._isPlaying) return;
        window.cancelAnimationFrame( (timestamp) => {this._update(timestamp)} );
        this._isPlaying = false;
    }

    /**
     * Timeline Update loop
     * @param {Number} timestamp - timestamp after next draw
     * @private
     */
    _update(timestamp) {

        if(!this._animationsReady) {
           return;
        }
        
        if (this._startTime === undefined) {
            this._startTime = timestamp;
        }
        const elapsed = timestamp - this._startTime;
    
        if(elapsed >= this._tlTimeDraw && this._isPlaying) {
            this._step();
            //this event is useful to rotate the compass when playing the timeline
            window.dispatchEvent(new CustomEvent("OnUpdateTimeline" , {
                bubbles : false
            }));
            this._startTime = timestamp;
        }

        window.requestAnimationFrame( (timestamp) => {this._update(timestamp)} );

    }

    /**
     * play one frame in the desired direction
     * @param {Boolean} reverse - if true we step the animation reversed
     * /@private
     */
    _step(reverse){
        reverse = (reverse == undefined) ? this._reverse : reverse;
        if(reverse) {
            this._setPrevIndex();
        }else{
            this._setNextIndex();
        }
        this._showAnimationFrame(this._animationIndex , this._frameIndex);
    }



    /**
     * draw call with request animation frame,
     * see if the browser is ready to draw 
     * @public
     */
     requestFrameDraw() {
        window.requestAnimationFrame( () => {this._draw()} );
    }

    /**
     * draw the current frame,
     * draw the hotspots,
     * load and draw the tiles.
     * @private
     */
    _draw(){

        //dont draw if animator is not ready 
        if(!this._animationsReady) {
            return;
        }

        
        // getting info about the draw size , frame , scaleFactor
        const anim = this._getAnimation();
        this._currentFrame = anim.findFrame(this._frameIndex);
        const scaleFactor = this._inputSystem.zoom.currentScale;

        // calculate the current width and height
        this._currentWidth = this.maxWidth * scaleFactor;
        this._currentHeight = this.maxHeight * scaleFactor;

        
        // setting zoom offset based on zooming points
        this._setZoomOffset();

        // calculating x y coordinates from the center of the canvas minus zoom offsets
        let x = Math.round( ( this._animCanvas.width*0.5 - this._currentWidth*0.5 ) - this._offsetX );
        let y = Math.round( (this._animCanvas.height*0.5 - this._currentHeight*0.5) - this._offsetY );

        // width is to small to zoom in/out, so centering the frame is the best option
        if(this._animCanvas.width < this.mobileWidth) {
            x = Math.round( this._animCanvas.width*0.5 - this._currentWidth*0.5 );
            y = Math.round( this._animCanvas.height*0.5 - this._currentHeight*0.5 );
            this._offsetX = 0;
            this._offsetY = 0;
        }

        
        // clearing the draw canvases , tiles must be cleared before the anim ctx(preloaded sizes canvas) 
        this._tilesCtx.clearRect(0, 0, this._tilesCanvas.width, this._tilesCanvas.height);
        this._animCtx.clearRect(0, 0, this._animCanvas.width, this._animCanvas.height);

        /**
         * draw tiles if the canvas exist.
         * Promise resolve canvas containing tiles or reject
         */
         const animIndex = this._animationIndex;
         const frameIndex = this._frameIndex;
         const offsetX = this._offsetX;
         const offsetY = this._offsetY;

        const pr = this._tilesController.fetchTiles(animIndex , frameIndex , this._currentWidth , scaleFactor);
        pr.then(canvas => {
            //check if scale is the same after loading the tile
            const scale = this._inputSystem.zoom.currentScale;
            if (
                scale !== scaleFactor || 
                animIndex !== this._animationIndex || 
                frameIndex !== this._frameIndex ||
                offsetX !== this._offsetX ||
                offsetY !== this._offsetY
            ) return;
            
            this._tilesCtx.drawImage(canvas , x, y , this._currentWidth , this._currentHeight);

        }).catch(() => {
            return;
        });


        // draw hotspots
        this.hotspots.drawHts(this._animationIndex , this._frameIndex , this._currentWidth , this._currentHeight , this._offsetX , this._offsetY);

        // draw the preloaded frame
        this._animCtx.drawImage(this._currentFrame , x, y , this._currentWidth , this._currentHeight);
        
        
    }

    _setZoomOffset() {

        if(this._offsetX === undefined) this._offsetX = 0;
        if(this._offsetY === undefined) this._offsetY = 0;

        if(this._zoomPoints === undefined) {
            return;
        }
        
        const screenCenterX = this._animCanvas.width*0.5;
        const screenCenterY = this._animCanvas.height*0.5;
        const xPivot = screenCenterX - (this._currentWidth*0.5) + this._offsetX;
        const yPivot = screenCenterY - (this._currentHeight*0.5) + this._offsetY;
        const framePosX = xPivot + this._currentWidth*0.5;
        const framePosY = yPivot + this._currentHeight*0.5;
        
        let zPointx;
        let zPointy;
        
        if(!this._inputSystem.zoom.isZoomIn || this._zoomPoints == -1){            
            zPointx = screenCenterX - framePosX ;
            zPointy = screenCenterY - framePosY ;
        }else{
            zPointx = this._zoomPoints.x - framePosX;
            zPointy = this._zoomPoints.y - framePosY;
        }
        
        const speed = 0.1;
        const maxMagnitude = this._animCanvas.width * 0.1;
        const angle = Math.atan2(zPointy , zPointx);

        let magnitude = Math.sqrt(Math.pow(zPointx , 2) + Math.pow(zPointy , 2));
        // limit the magnitude to avoid high speed zooming animation
        if(magnitude > maxMagnitude) magnitude = maxMagnitude;

        this._offsetX += Math.round( (magnitude * Math.cos(angle)) * speed );
        this._offsetY += Math.round( (magnitude * Math.sin(angle)) * speed );

        this._zoomPoints = undefined;
    }

    /**
     * Switch to a desired Animation
     * @param {Number} index - the animation index
     */
    switchAnimation(index) {
        if(index < 0 || index >= this._animations.length) {
            return;
        }

        this._showAnimationFrame(index , this._frameIndex);
    }


    /**
     * 
     * @param {Number} animIndex - the animation index
     * @param {Number} frameIndex - the frame index
     * @private
     */
    _showAnimationFrame (animIndex , frameIndex) {
        this._animationIndex = animIndex;
        this._frameIndex = frameIndex;
        this.requestFrameDraw();
    }

    

    /**
     * Play one frame
     * @param {Boolean} reverse - is reversed direction
     * @public
     */
    playFrame(reverse) {
        this.stop();
        this._step(reverse);
    }


    /**
     * get the current animation
     * @returns {Animation} the current animation
     * @private
     */
    _getAnimation() {
        return this._animations[this._animationIndex];
    }

    _setNextIndex() {
        this._frameIndex++;
        if (this._frameIndex >= this._countFrames) {
            this._frameIndex = 0;
        }
    }

    _setPrevIndex() {
        this._frameIndex--;
        if (this._frameIndex < 0) {
            this._frameIndex = this._countFrames-1;
        }
    }

    /**
     * draw when zoom changed
     * @private
     */
    _onZoomEvent(){
        this._zoomLerp();
        this.stop();
        this.requestFrameDraw();
    }

    _zoomLerp () {
        const points = this._inputSystem.zoom.zoomPoints;

        if(points == -1) {
           /*  this._zoomPoints.x = 0;
            this._zoomPoints.y = 0; */
            this._zoomPoints = -1;
            return;
        }

        this._zoomPoints = points;
        
    }

    /**
     * draw when drag controls changed
     * @private
     */
    onDragEvent(e){
        this.playFrame(e.detail);
    }

}


export { Animator };