import { Animator } from "./animator";
import { CanvasBuilder } from "./canvasBuilder";
import { XmlManager } from "@/utils/helpers";

import APP_DATA  from "@/assets/appData.json";

class Orbital {
    constructor(container , regionsInfo){
        
        this.xml;

        this.container = container;

        this._regionsInfo = regionsInfo;

        this.animationsPaths = [];

        this.animator, this.canvasBuilder;


    }

    init(){

        this.xml = new XmlManager("config.xml");

        this.xml.loadXmlData(() => {
            this.htFromXml = this.xml.getHotspots();
            this._setup();
        });
    }

    _setup() {
        
        const params = APP_DATA.params;
        const levelsCount = params.animationsCount;
        let lPath = params.imagesFolder + params.preloadedFolder;
        
        for (let i = 0; i < levelsCount; i++) {
            
            const p = lPath.replace(/%row/g , i).replace(/%col/g , "?");
            this.animationsPaths.push(p);
        }

        this.canvasBuilder = new CanvasBuilder(this.container);

        const layers = this.canvasBuilder.createLayers();

        const options = {
            maxWidth : params.maxWidth,
            maxHeight : params.maxHeight,
            imagesFolder : params.imagesFolder,
            framesCount : params.framesCount,
            timeLineSpeed : params.timeLineSpeed,
            autoplay : params.autoplay,
            frameStart : params.frameStart,
            animStart : params.animStart,
            
            tilesFolder : APP_DATA.tiles.tilesFolder,
            breakpoints_sizes : APP_DATA.tiles.breakpoints_sizes,
            breakpoints : APP_DATA.tiles.breakpoints,
            breakpoints_folder : APP_DATA.tiles.breakpoints_folder,
            maxCache : APP_DATA.tiles.maxTilesCache,
            cropSize : APP_DATA.tiles.cropSize,

            zoomOptions : APP_DATA.controls.zoom,
            dragOptions : APP_DATA.controls.drag,
        };


        this.animator = new Animator(
            layers,
            this.animationsPaths ,
            options,
            this.htFromXml,
            this._regionsInfo
        );

        window.addEventListener("OnAnimatorReady" , () => this.appReady() , {once : true});
        this._setConfigStyles();

    }

    _setConfigStyles(){
        if(this.animator === undefined) return;
        document.getElementById(this.container).style.backgroundColor = APP_DATA.params.bg_color;
    }

    appReady() {

        this.animationsPaths = null;

        this.animator.run();

        window.dispatchEvent(new CustomEvent("OnAppReady", {
            bubbles: false
        }));
    }
    

    getPercentLoaded() {
        if(this.animator === undefined) return 0;
        return this.animator.percentLoaded;
    }

    playOrbital(){
        this.animator.play();
    }

    stopOrbital(){
        this.animator.stop();
    }

    next(){
        this.animator.playFrame(false);
    }

    prev(){
        this.animator.playFrame(true);
    }

    setLevel(level){
        this.animator.switchAnimation(level);
    }
    
    filterHSByPrice(surface) {
        if(this.animator === undefined) return;
        this.animator.stop();
        this.animator.hotspots.setSurfaceFilter(surface);
    }
    filterHSByTypes(types) {
        if(this.animator === undefined) return;
        this.animator.stop();
        this.animator.hotspots.setTypesFilter(types);
    }

    setZoomOptions (opt){
        this.animator._inputSystem.zoom.setOptions(opt);
    }

    setZoom(zoomIn) {
        if(this.animator === undefined) return;
        this.animator._inputSystem.zoom.setZoom(zoomIn);
    }

    getParam(param){
        return APP_DATA.params[param];
    }
    getHSParam(param){
        return APP_DATA.hotspots[param];
    }

    getCurrentFrame(){
        if(this.animator === undefined) return;
        return this.animator._frameIndex;
    }

    unmountOrbital() {
        if(this.animator === undefined) return;
        this.animator.cancel = true;
    }
    
}

export {Orbital}