<template>
    <div class="filter position-fixed p-0" :style="style" ref="filterParent">
        <div class="d-flex justify-content-center">

            <div id="filter" class="toast" role="alert" aria-live="assertive" aria-atomic="true" data-bs-autohide="false"
                ref="filter">
                <div class="toast-header bg-dark text-white" ref="draggableCapture">
                    <strong class="me-auto">Filtrage de Lot</strong>
                    <button type="button" class="btn-close btn-close-white" @click="closeToast"></button>
                </div>
                <div class="toast-body">

                    <slider-filter @onSurfaceChanged="surfaceChanged" :min="surfaceMinMax.min" :max="surfaceMinMax.max"
                        :step="0.1" :gap="2" :slider-delay="300">
                    </slider-filter>

                    <hr />

                    <types-filter :default-types="checkedTypes" @onTypesChanged="typesChanged"></types-filter>

                </div>
            </div>
        </div>
    </div>
</template>


<script setup>

import { Toast } from "bootstrap";

import SliderFilter from "@/components/orbital/search/SliderFilter";
import TypesFilter from "@/components/orbital/search/TypesFilter";

import { onClickOutside, useDraggable } from '@vueuse/core'

import { ref, reactive, watch, onMounted, onUnmounted } from "vue";

import { useRegionsStore } from "@/stores/regions";
import { useAppDataStore } from "@/stores/appData";

import { storeToRefs } from 'pinia';

const regionsStore = useRegionsStore();
const appDataStore = useAppDataStore();

const emits = defineEmits([
    "onCloseToast",
    "onSurfaceChanged",
    "onTypesChanged"
]);


const props = defineProps(
    {
        showToast: {
            type: Boolean,
            default: true
        },
    }
);


const filterParent = ref(null);
const filter = ref(null);
const draggableCapture = ref(null);

const { defaultFilters } = storeToRefs(regionsStore);
const { surfaceMinMax } = storeToRefs(appDataStore);
const checkedTypes = ref(defaultFilters.value.types);

let toast = null;
const appReady = ref(false);
const surface = reactive({
    min: surfaceMinMax.value.min,
    max: surfaceMinMax.value.max
});


const { style, position } = useDraggable(filterParent, {
    exact: false,
    handle: draggableCapture,
    pointerTypes: ['mouse', 'touch', 'pen'],
    onMove: (pos) => {
        const elem = filterParent.value;
        const elemWidth = elem.offsetWidth;
        const elemHeight = elem.offsetHeight;
        const windowWidth = window.innerWidth;
        const windowHeight = window.innerHeight;

        if (pos.x < 0) {
            position.value.x = 0;
        }
        if (pos.x > (windowWidth - elemWidth)) {
            position.value.x = windowWidth - elemWidth;
        }

        if (pos.y < 0) {
            position.value.y = 0;
        }

        if (pos.y > (windowHeight - elemHeight)) {
            position.value.y = windowHeight - elemHeight;
        }
    }
});


onClickOutside(filterParent, () => {
    closeToast();
});




function initToast() {
    toast = new Toast(filter.value);
    toast.show(); //showing the toast before hidding it to get his size
    toogleToast();
}
function toogleToast() {
    if (props.showToast) {
        toast.show();
    } else {
        toast.hide();
    }
}

function closeToast() {
    emits("onCloseToast");
}


function surfaceChanged(sur = surface) {

    if (appReady.value) {
        surface.min = sur.min;
        surface.max = sur.max;
        emits("onSurfaceChanged", surface);
    }
}

function typesChanged(types = defaultFilters.value.types) {
    if (appReady.value) {
        checkedTypes.value = types;
        emits("onTypesChanged", checkedTypes.value);
    }
}

function _appReady() {
    appReady.value = true;
    surfaceChanged();
    typesChanged();
}


watch(() => props.showToast, () => {
    toogleToast();
});



onMounted(() => {
    initToast();
    position.value.x = (window.innerWidth * 0.5) - (filterParent.value.offsetWidth * 0.5);
    position.value.y = (window.innerHeight * 0.5) - (filterParent.value.offsetHeight * 0.5);

    window.addEventListener("OnAppReady", _appReady);
});

onUnmounted(() => {
    window.removeEventListener("OnAppReady", _appReady);
});

</script>



<style lang="scss" scoped>
.filter {
    z-index: 1030;
    width: auto;
    user-select: none;

    .toast {
        width: 25em;

        .toast-header {
            letter-spacing: .13em;
            font-size: .9em;
            font-weight: 500;

            &:hover {
                cursor: move;
            }
        }

    }
}
</style>