<template>
    <div class="slider-parent">


        <div class="slider">
            <div class="fs-6 text-secondary text-center ">
                <span>Surface en M²</span>
            </div>
            <div class="position-relative ranges">
                <input type="range" class="form-range position-absolute" id="min-range" ref="minSlider" :max="max"
                    :min="min" :step="step" v-model="minValue" @input="minSliderInput">
                <input type="range" class="form-range position-absolute" id="max-range" ref="maxSlider" :max="max"
                    :min="min" :step="step" v-model="maxValue" @input="maxSliderInput">
            </div>
        </div>

        <div class="range-info d-flex justify-content-between align-items-center my-4">
            <div class="form-floating mx-4 mx-sm-1">
                <input type="number" id="min-input" class="form-control" :value="minValue" @input="debounceMin">
                <label for="min-input">min</label>
            </div>

            <div class="form-floating mx-4 mx-sm-1">
                <input class="form-control" type="number" id="max-input" :value="maxValue" @input="debounceMax">
                <label for="max-input">max</label>
            </div>
        </div>
    </div>
</template>




<script setup>

import debounce from "lodash/debounce";
import { ref, reactive, watch, onMounted, onUnmounted } from "vue";


const emits = defineEmits([
    "onSurfaceChanged"
]);


const props = defineProps({

    min: {
        type: Number,
        default: 0
    },
    max: {
        type: Number,
        default: 100
    },
    step: {
        type: Number,
        default: 1
    },
    gap: {
        type: Number,
        default: 10
    },
    sliderColor: {
        type: String,
        default: "#cecece"
    },
    rangeColor: {
        type: String,
        default: "#21af4f"
    },

    sliderDelay: {
        type: Number,
        default: 1000
    },

    inputsDelay: {
        type: Number,
        default: 800
    }
});



const minValue = ref(props.min);
const maxValue = ref(props.max);

const surface = reactive({
    min: minValue.value,
    max: maxValue.value
})

const maxSlider = ref(null);

function initSliders() {
    fillSlider(minValue.value, maxValue.value);
    surfaceChanged();
}


function controlSlider(isMinSlider) {
    const gap = Number(props.gap);
    const min = Number(minValue.value);
    const max = Number(maxValue.value);
    const validMax = max - gap;

    if (min < props.min) {
        minValue.value = props.min;
        fillSlider(min, max);
        return;
    }

    if (max > props.max) {
        maxValue.value = props.max;
        fillSlider(min, max);
        return;
    }

    if (isMinSlider) {
        if (min > validMax) {
            minValue.value = validMax;
            fillSlider(validMax, max);
            return;
        }
        fillSlider(min, max);
        return;
    }

    if (minValue.value > validMax) {
        maxValue.value = min + gap;
        fillSlider(min, maxValue.value);
        return;
    }
    fillSlider(min, max);

}

function fillSlider(from, to) {

    if (!maxSlider.value) return;
    const rangeDistance = props.max - props.min;
    const fromPosition = from - props.min;
    const toPosition = to - props.min;
    const sliderColor = props.sliderColor;
    const rangeColor = props.rangeColor;

    maxSlider.value.style.background = `linear-gradient(
            to right,
            ${sliderColor} 0%,
            ${sliderColor} ${(fromPosition) / (rangeDistance) * 100}%,
            ${rangeColor} ${((fromPosition) / (rangeDistance)) * 100}%,
            ${rangeColor} ${(toPosition) / (rangeDistance) * 100}%, 
            ${sliderColor} ${(toPosition) / (rangeDistance) * 100}%, 
            ${sliderColor} 100%)`;
}

function surfaceChanged() {
    emits("onSurfaceChanged", surface);
}



//SLIDERS
function minSliderInput() {
    controlSlider(true)
    debounceMinSlider();
}
function maxSliderInput() {
    controlSlider(false)
    debounceMaxSlider();
}
const debounceMinSlider = debounce(() => {
    surface.min = minValue.value;
}, props.sliderDelay);
const debounceMaxSlider = debounce(() => {
    surface.max = maxValue.value;
}, props.sliderDelay);



//INPUTS
const debounceMin = debounce((evt) => {
    minValue.value = evt.target.value;
    surface.min = minValue.value;
    controlSlider(true)
}, props.inputsDelay);

const debounceMax = debounce((evt) => {
    maxValue.value = evt.target.value;
    surface.max = maxValue.value;
    controlSlider(false);
}, props.inputsDelay);


watch(surface, () => {
    surfaceChanged();
});


onMounted(() => {
    initSliders();
});

onUnmounted(() => {
});

</script>




<style lang="scss" scoped>
.ranges {
    margin: 1em 0;
    min-height: 1em;
}

input[type=range] {
    height: 0.3em;
    width: 100%;
    position: absolute;
    background-color: #C6C6C6;
    pointer-events: none;
    border-radius: 2em;
}

input[type=range]::-moz-range-thumb {
    cursor: pointer;
    pointer-events: all;
}


input[type=range]::-webkit-slider-thumb {
    cursor: pointer;
    pointer-events: all;
}


input[type=range]::-moz-range-track {
    background-color: transparent;

}

input[type=range]::-webkit-slider-runnable-track {
    background-color: transparent;

}


#min-range {
    top: 0.15em;
    height: 0;
    z-index: 1;
}
</style>