
class Animation{

    /**
     * @constructor
     * @param {String} fileNameTemplate name of animation images , $ is replaced with id
     * @param {Number} countSprite total of frames
     * @param {Function} onFinishLoading callback when animation is loaded , return error if not loaded
     * @param {Function} onLoadingChange callback when an image is added
     */
    constructor(fileNameTemplate , countSprite , onFinishLoading , onLoadingChange) {
        this._images = [];
        this._countSprite = countSprite;
        this.imgLoaded = 0;


        this._loadAnimation(fileNameTemplate , onFinishLoading , onLoadingChange);
    }

    /**
     * 
     * @param {String} fileNameTemplate
     * @param {Function} onFinishLoading 
     * @private
     */
    _loadAnimation(fileNameTemplate , onFinishLoading , onLoadingChange) {
        let allPromises = [];
        for (let i = 0; i < this._countSprite; i++) {
            
            const p = new Promise( (resolve , reject) => {
                // replace ? with image id
                const path = fileNameTemplate.replace('?' , i);
    
                // require and load the image
                const imgPath = require('@/' + path);
                const image = new Image();
                image.src = imgPath;
                this._images.push(image);
                //console.log(this.images[i]);
                image.addEventListener('load' , () => {
                    this.imgLoaded ++;
                    onLoadingChange();
                    resolve();
                });

                image.addEventListener('error' , (e) => {
                    console.log(e);
                    reject(e);
                });
            });

            //add the promise to the stack
            allPromises.push(p);
        }

        // using Promise.all ensure the frames Multi-thread loaded
        Promise.all(allPromises).then(() => {
            onFinishLoading();
        }).catch( (e) => {
            onFinishLoading(e);
        } )
    }


    findFrame(index) {
        return this._images[index];
    }

}


export { Animation };