class CanvasBuilder {

    /**
     * 
     * @param {String} parentId - the app container ID
     */
    constructor (parentId) {

        this._parentId = parentId;
        this._parentElem;
    }

    createLayers() {

        /**
         * chek if there is a valid container
         */
        this._parentElem = document.getElementById(this._parentId);
        if(!this._parentElem) {
            alert("Orbital dom element not found !! " + this._parentId);
            throw new Error ("Orbital dom element not found !!" + this._parentId);
        }

        //setup the layers
        const eventsCanvas = this.createCanvas("eventsCanvas" , 99);
        const htEffectCanvas = this.createCanvas("htEffectCanvas" , 5);
        const htDrawCanvas = this.createCanvas("htDrawCanvas" , 4);
        const tilesCanvas = this.createCanvas("tilesCanvas" , 3);
        const animCanvas = this.createCanvas("animCanvas" , 2);
        const htRefCanvas = this.createCanvas("htRefCanvas" , 1);
        
        htRefCanvas.style.left = "-300%";
        htRefCanvas.style.visibility = "hidden";

        //add and return the canvas group
        const layers = {
            animation :  animCanvas,
            hotspotsRef : htRefCanvas,
            hotspotsDraw : htDrawCanvas ,
            hotspotsEffects : htEffectCanvas,
            eventsCanvas : eventsCanvas,
            tilesCanvas : tilesCanvas
        }
        return layers;
        
    }

    /**
     * 
     * @param {Number} id - give the canvas element an ID
     * @returns {HTMLCanvasElement} the created canvas
     */
    createCanvas (id , zId){
        if(this._parentElem === null) return;
        const canvas = document.createElement('canvas');
        canvas.id     = id;
        canvas.width  = document.documentElement.clientWidth;
        canvas.height = document.documentElement.clientHeight;
        //for our issue in oldest mobiles of the overflow, we set position to be fixed
        canvas.style.position = "fixed"; 
        canvas.style.zIndex = zId;
        canvas.style.top = 0;
        canvas.style.left = 0;

        this._parentElem.appendChild(canvas);
        return canvas;
    }
}


export {CanvasBuilder}