<template>
    <div class="parent position-relative h-100 p-0 m-0 border border-0">

        <capture-modal />

        <LoaderComponent :is-error="orbitalError" :show="showLoader" :percent-loaded="percentLoaded" @on-refresh="refresh">
        </LoaderComponent>

        <HotspotDetails :canShow="showHsDetails" @onCloseDetails="showHsDetails = false" ref="detailsComponent">
        </HotspotDetails>

        <OrbitalMenu :stage-count="stageCount" :starting-at="startStage" @on-play="play" @on-step="step"
            @on-step-end="endTimout" @on-set-level="setLevel" @on-show-filter="showFilter" @on-zoom="setZoom"
            @on-zoom-end="endTimout">
        </OrbitalMenu>

        <div class="orbital-canvas p-0 m-0 border border-0 h-100 position-relative" id="orbital" ref="screenshot">
            <compass-component ref="compass"></compass-component>
            <PopoverComponent :show-popover="showPopper" :positions="popoverPos"></PopoverComponent>
        </div>

        <FilterComponent :default-filters="default_filters" :show-toast="showToast" @onCloseToast="closeFilter"
            @onSurfaceChanged="surfaceChanged" @onTypesChanged="typesChanged">
        </FilterComponent>

        <div class="s-f-menu d-flex">
            <button class="btn custom-btn btn-sm btn-style fullscreen-btn" type="button" @click="toggle">
                <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor"
                    class="bi bi-arrows-fullscreen" viewBox="0 0 16 16">
                    <path fill-rule="evenodd"
                        d="M5.828 10.172a.5.5 0 0 0-.707 0l-4.096 4.096V11.5a.5.5 0 0 0-1 0v3.975a.5.5 0 0 0 .5.5H4.5a.5.5 0 0 0 0-1H1.732l4.096-4.096a.5.5 0 0 0 0-.707zm4.344 0a.5.5 0 0 1 .707 0l4.096 4.096V11.5a.5.5 0 1 1 1 0v3.975a.5.5 0 0 1-.5.5H11.5a.5.5 0 0 1 0-1h2.768l-4.096-4.096a.5.5 0 0 1 0-.707zm0-4.344a.5.5 0 0 0 .707 0l4.096-4.096V4.5a.5.5 0 1 0 1 0V.525a.5.5 0 0 0-.5-.5H11.5a.5.5 0 0 0 0 1h2.768l-4.096 4.096a.5.5 0 0 0 0 .707zm-4.344 0a.5.5 0 0 1-.707 0L1.025 1.732V4.5a.5.5 0 0 1-1 0V.525a.5.5 0 0 1 .5-.5H4.5a.5.5 0 0 1 0 1H1.732l4.096 4.096a.5.5 0 0 1 0 .707z" />
                </svg>
            </button>

            <button class="btn custom-btn btn-sm btn-style capture-btn" type="button"
                @click="captureStore.takeScreen(screenshot)">
                <svg xmlns="http://www.w3.org/2000/svg" width="1.5em" height="1.5em" fill="currentColor"
                    class="bi bi-camera" viewBox="0 0 16 16">
                    <path
                        d="M15 12a1 1 0 0 1-1 1H2a1 1 0 0 1-1-1V6a1 1 0 0 1 1-1h1.172a3 3 0 0 0 2.12-.879l.83-.828A1 1 0 0 1 6.827 3h2.344a1 1 0 0 1 .707.293l.828.828A3 3 0 0 0 12.828 5H14a1 1 0 0 1 1 1v6zM2 4a2 2 0 0 0-2 2v6a2 2 0 0 0 2 2h12a2 2 0 0 0 2-2V6a2 2 0 0 0-2-2h-1.172a2 2 0 0 1-1.414-.586l-.828-.828A2 2 0 0 0 9.172 2H6.828a2 2 0 0 0-1.414.586l-.828.828A2 2 0 0 1 3.172 4H2z" />
                    <path
                        d="M8 11a2.5 2.5 0 1 1 0-5 2.5 2.5 0 0 1 0 5zm0 1a3.5 3.5 0 1 0 0-7 3.5 3.5 0 0 0 0 7zM3 6.5a.5.5 0 1 1-1 0 .5.5 0 0 1 1 0z" />
                </svg>
            </button>
        </div>



    </div>
</template>



<script setup>



import { Orbital } from "@/canvas-modules/orbital";

import CaptureModal from "@/components/app-ui/CaptureModal.vue";
import CompassComponent from "./CompassComponent";
import FilterComponent from "./FilterComponent";
import HotspotDetails from "./HotspotDetails";
import LoaderComponent from "./LoaderComponent";
import OrbitalMenu from "./OrbitalMenu";
import PopoverComponent from "./PopoverComponent";

import { useFullscreen } from '@vueuse/core';
import { ref, reactive, computed, onMounted, onUnmounted } from "vue";
import { storeToRefs } from "pinia";
import { useRegionsStore } from "@/stores/regions";
import { useCaptureStore } from "@/stores/capture";



const { toggle } = useFullscreen();

const captureStore = useCaptureStore();

const regionsStore = useRegionsStore();
const { regions, colorTypes, default_filters, orbitalParams, firstFetch, isError } = storeToRefs(regionsStore);

const screenshot = ref(null), detailsComponent = ref(null);

const showLoader = ref(true);

const showToast = ref(false);

const showPopper = ref(false), popoverPos = reactive({ x: 0, y: 0 });

const orbitalError = ref(false);
let orbital = null;
const showHsDetails = ref(false),
    hsData = ref({}),
    stageCount = ref(1),
    typesColors = ref({}),
    currentColor = ref("#cccccc"),
    percentLoaded = ref(0),
    startingStage = ref(0);
const startStage = computed(() => {
    return Math.min(Math.max(parseInt(startingStage.value), 0), stageCount.value - 1);
});

const MenuBtnInterval = ref(undefined);

const compass = ref(null);
const angle = ref(0);
const northAngle = ref(orbitalParams.value.northAngle);


/***********************************
  Initialisation
***********************************/
function loadRegionsInfo() {

    if (!firstFetch.value) return init();

    regionsStore.fetchRegions().then((err) => {
        if (isError.value) {
            console.log(err);
            orbitalError.value = true;
            return;
        }
        init();
    });

}

function init() {
    orbital = new Orbital("orbital", regions.value);
    orbital.init();

    stageCount.value = orbitalParams.value.animationsCount;
    startingStage.value = orbitalParams.value.animStart;
    typesColors.value = colorTypes.value;

    addEvents();
}
/***********************************
  END Initialisation
***********************************/




/***********************************
  Inner functions
***********************************/
function setLevel(level) {
    orbital.setLevel(level);
}

function play(play) {
    if (play) {
        return orbital.playOrbital();
    }
    orbital.stopOrbital();
}

function step(reverse) {
    window.clearInterval(MenuBtnInterval.value);
    if (reverse)
        orbital.prev();
    else orbital.next();
    _rotateCompass();

    MenuBtnInterval.value = window.setInterval(() => {
        if (reverse)
            orbital.prev();
        else orbital.next();
        _rotateCompass();
    }, 100);
}

function _setOrbitalAngle() {
    const totalFrames = orbitalParams.value.framesCount;
    angle.value = Math.round(360 / totalFrames);
}

function _rotateCompass() {
    const frame = orbital.getCurrentFrame();
    const a = northAngle.value + angle.value * frame;
    compass.value.rotate(a);
}

function setZoom(zoomIn) {
    window.clearInterval(MenuBtnInterval.value);
    orbital.setZoom(zoomIn);
    MenuBtnInterval.value = window.setInterval(() => orbital.setZoom(zoomIn), 30);
}
function endTimout() {
    window.clearInterval(MenuBtnInterval.value);
}
function showFilter() {
    showToast.value = true;
}

function closeFilter() {
    showToast.value = false;
}

function surfaceChanged(surface) {
    orbital.filterHSByPrice(surface);
}

function typesChanged(types) {
    orbital.filterHSByTypes(types);
}

function refresh() {
    orbitalError.value = false;
    percentLoaded.value = 0;
    loadRegionsInfo();
}
/***********************************
  END Inner functions
***********************************/








/***********************************
  Events functions
***********************************/
function onAppReady() {
    showLoader.value = false;
    _setOrbitalAngle();
    _rotateCompass();
    orbital.animator.canvasGroup.eventsCanvas.addEventListener('OnDrag', onDrag);
}

function onUpdateTimeline() {
    _rotateCompass();
}

function onError() {
    orbitalError.value = true;
}

function onLoadingChange() {
    percentLoaded.value = orbital.getPercentLoaded();
}

function onDrag() {
    _rotateCompass();
}

function onHSClick(e) {
    const detail = e.detail;
    const region = detail.region;
    if (region === undefined) return;
    hsData.value = region;
    regionsStore.setRegion(region.nom);
    detailsComponent.value.setImageSrc();
    showHsDetails.value = true;
}

let lastRegionBody = null;
function onHSHover(e) {

    const detail = e.detail;
    const region = detail.region;

    if (region === undefined || !Object.keys(regions.value).length) return;

    const pos = e.detail.mousePos;

    showPopper.value = true;

    popoverPos.x = pos.x;
    popoverPos.y = pos.y;

    const regionBody = regions.value[region.name];

    if (lastRegionBody === regionBody) return;

    lastRegionBody = regionBody;
    const type = regionBody.type;

    currentColor.value = typesColors.value[type];

    hsData.value = regionBody;

    regionsStore.setRegion(region.name);
}

function onHSOut() {
    showPopper.value = false;
}

/***********************************
  END Events functions
***********************************/





onMounted(() => {
    loadRegionsInfo();
});

onUnmounted(() => {
    clearEvents();
    orbital.unmountOrbital();
});


function addEvents() {
    window.addEventListener("OnLoadingChange", onLoadingChange);
    window.addEventListener("OnUpdateTimeline", onUpdateTimeline);
    window.addEventListener("OnError", onError);
    window.addEventListener("OnAppReady", onAppReady);
    window.addEventListener("OnHSClick", onHSClick);
    window.addEventListener("OnHSHover", onHSHover);
    window.addEventListener("OnHSHoverOut", onHSOut);
}

function clearEvents() {
    window.removeEventListener("OnLoadingChange", onLoadingChange);
    window.removeEventListener("OnUpdateTimeline", onUpdateTimeline);
    window.addEventListener("OnError", onError);
    window.removeEventListener("OnAppReady", onAppReady);
    window.removeEventListener("OnHSClick", onHSClick);
    window.removeEventListener("OnHSHover", onHSHover);
    window.removeEventListener("OnHSHoverOut", onHSOut);
}

</script>


<style lang="scss" scoped>
.s-f-menu {
    position: absolute;
    right: 0;
    bottom: 0;
    z-index: 1000;
}
</style>
