class Drag {
    /**
     * control dragging events
     * @param {HTMLCanvasElement} rect - canvas events capturer
     * @param {object} dragOptions - drag options
     */
     constructor(rect , dragOptions) {
        this.rect = rect;

        // drag parameters
        this._sensitivity = dragOptions.sensitivity; // drag sensitivity, heigher value for faster animation
        this._threshold = dragOptions.threshold; // drag differnce to reach for a step
        this._throwSens = dragOptions.throwSens; // drag throwing sensitivity , heigher value for faster animation
        this._maxVelocity = dragOptions.maxVelocity; 
        /**
         * when dragging with height velocity, we capture the number of frames to be played
         * heigher value for faster animation
         */
        this._maxStepsCaptures = 50; 
        this.velocity = undefined;

        //this._dragWait = 0;


        this.isDragging = false;
        this.reverse = true;

        this._prevX = undefined;
        this._prevT = undefined; // the previus time when dragging, useful to calculate the velocity
        this._lastDiff = undefined; // the last drag difference
        this._bounding;

        this._waitTimout;

        this.init();
    }

    /**
     * get the canvas bounding rect
     */
    get _bounding(){
        return this.rect.getBoundingClientRect();
    }

    init() {
        this.clear();
    }

    /**
     * clear the dragging params
     */
    clear(){
        this.isDragging = false;
        this._prevX = undefined;
        this._prevT = undefined;
        this._lastDiff = undefined;
        clearTimeout(this._waitTimout);
    }

    //#region Mouse interactions
    mouseDown(e){
        this._startEvent(e.clientX);
    }

    mouseMove(e){
        
        if(!this.isDragging) return;
        this._rotate(e.clientX);
    }

    mouseUp(){
        this._endEvent();
    }
    //#endregion

    //#region Touch interactions
    touchStart(e) {
        const touches = e.touches;
        if(touches.length > 1) {
            this.clear();
            return
        }
        this._startEvent(touches[0].clientX);
    }

    touchMove(e) {
        if(!this.isDragging) return;
        const touches = e.touches;
        if(touches.length > 1) {
            this.clear();
            return
        }
        
        this._rotate(touches[0].clientX);
    }

    touchEnd() {
        this._endEvent();
    }
    //#endregion


    //#region Drag setup
    _startEvent (clientX){
        this.isDragging = true;
        this._prevX = this._getCurrentX(clientX);
        this._prevT = Date.now();
        clearTimeout(this._waitTimout);
    }
    _endEvent (){
        this.isDragging = false;
        this._throw();
        this._lastDiff = undefined;
    }
    //#endregion



    /**
     * animate when dragging
     * @param {Number} clientX - x coordinate of the click/touch event
     */
    async _rotate(clientX){
        
        
        //get the current x of the interaction
        const currentX = this._getCurrentX(clientX);
        //calculate the difference between the last pos and the current pos
        this._lastDiff = Math.round(currentX - this._prevX);
        const new_t = Date.now();
        const elapsed = new_t - this._prevT;
        //this.velocity = Math.sqrt(Math.pow(this._lastDiff , 2))/elapsed;
        this.velocity = Math.abs(this._lastDiff/elapsed);

        //compare the absolute difference and the mouse threshold
        if(Math.abs(this._lastDiff) > this._threshold) {
            
            //check if direction is reversed
            this.reverse = this._lastDiff > 0;

            /**
             * play animation based on the delta x movement,
             * await the acceleration interval,
             * then we drag the next iteration
             */

            const condition = Math.floor(this.velocity * this._sensitivity);
            //console.log(condition);
            for (let i = 0; i <= condition ; i++) {

                
                // acceleration timer
                await this._wait(this.velocity);

                /**
                 * bind the OnDrag Event
                 *  
                 */
                this.rect.dispatchEvent(new CustomEvent("OnDrag", {
                    bubbles: false,
                    // detail :bool , indicate the drag direction (!reversed : right)
                    detail : this.reverse
                }));

            }

            //save the current params
            this._prevX = currentX;
            this._prevT = new_t;
        }
    }

    /**
     * throwing system with acceleration 
     * @returns null
     */
    async _throw(){
        const absDiff = Math.abs(this._lastDiff);
        if (this.isDragging || this._lastDiff === undefined || absDiff < this._threshold) {
            this.clear();
            return;
        }

        // get the direction
        const reverse = this._lastDiff > 0;

        // saved captures when dragging , based on acceleration and mapped to the max captures
        let captures = Math.round( Math.abs(this.velocity) ) * this._throwSens;
        captures = captures > this._maxStepsCaptures ? this._maxStepsCaptures : captures;
        
        // mapping the velocity to the max velocity
        this.velocity = this.velocity > this._maxVelocity ? this._maxVelocity : this.velocity;
        // inverting the value based on the max velocity
        this.velocity = this._maxVelocity - this.velocity;

        for (let i = 0; i < captures ; i++) {
            if (this.isDragging) {
                this.clear();
                break;
            }
            
            await this._wait(this.velocity);
            this.rect.dispatchEvent(new CustomEvent("OnDrag", {
                bubbles: false,
                detail : reverse
            }));
            const ease = this._easeOutSine(i/captures);
            this.velocity += ease * this._sensitivity;
        }
    }
    
    /**
     * Sin ease function 
     * @param {Number} x value from 0 to 1
     * @returns easing value from 0 to 1
     */
    _easeOutSine(x) {
        return Math.sin((x * Math.PI) / 2);
    }


    /**
     * timer with promise
     * @returns {Promise} the wait promise
     */
     _wait(ms) {
        const p = new Promise( (resolve) => {
            this._waitTimout = setTimeout( () => {
                resolve();
            } , ms);
        } );
        
        return p;
    }


    /**
     * return the valid click or touch 
     * @param {Number} clientX the clientX of the interaction
     * @returns {Number} valid click/touch in the rect
     */
    _getCurrentX(clientX) {
        return clientX - this._bounding.left;
    }

}

export {Drag}