<template>
    <div class="status-parent">
        <div class="fs-6 text-center text-secondary mb-3">
            <span>Status</span>
        </div>

        <div class="d-flex justify-content-center text-white">

            <div class="me-2" v-for="(stat, index ) in status" :key="index">
                <input type="checkbox" class="btn-check" :id="stat.name + index" v-model="currentStatus" :value="stat.value"
                    autocomplete="off">
                <label class="btn custom-checkbox border-0 text-white text-start bg-success pe-4" :class="stat.css_type"
                    :for="stat.name + index">
                    {{ stat.name }}
                </label>
            </div>

        </div>
    </div>
</template>


<script setup>

import { ref, watch, onMounted } from "vue";

import { useAppDataStore } from "@/stores/appData";
import { storeToRefs } from "pinia";

const appDataStore = useAppDataStore();

const { status, defaultStatus } = storeToRefs(appDataStore);


const emits = defineEmits([
    "onStatusChanged"
]);

const currentStatus = ref(defaultStatus.value);

function statusChanged() {
    emits("onStatusChanged", currentStatus.value);
}

watch(currentStatus, () => {
    statusChanged();
});

watch(() => defaultStatus.value, () => {
    currentStatus.value = defaultStatus.value;
    statusChanged();
});

onMounted(() => {
    statusChanged();
})
</script>


