<template>
    <div class="types">

        <div class="fs-6 text-center text-secondary mb-3">
            <span>Types d'appartement</span>
        </div>

        <div class="d-flex justify-content-center">
            <div class="me-2 w-50 position-relative" v-for="(value, key, index) in colorTypes" :key="index">
                <input type="checkbox" class="btn-check" :id="'checkbox-' + index" :value="key" v-model="checkedTypes"
                    autocomplete="off">
                <label class="btn custom-checkbox border-0 text-white text-start w-100" :for="'checkbox-' + index"
                    :style="{ 'background-color': value }">
                    {{ key }}
                </label>
            </div>
        </div>
    </div>
</template>


<script setup>

import { ref, watch, onMounted } from "vue";
import { useRegionsStore } from "@/stores/regions";
import { storeToRefs } from 'pinia';

const regionsStore = useRegionsStore();

const { colorTypes } = storeToRefs(regionsStore);

const emits = defineEmits([
    "onTypesChanged"
]);

const props = defineProps({
    defaultTypes: {
        type: Array,
        default() {
            return []
        }
    }
});

const checkedTypes = ref(props.defaultTypes);


function typesChanged() {
    emits("onTypesChanged", checkedTypes.value);
}

watch(checkedTypes, () => {
    typesChanged();
});

watch(() => props.defaultTypes, () => {
    checkedTypes.value = props.defaultTypes;
    typesChanged();
});

onMounted(() => {
    typesChanged();
});


</script>

<style lang="scss" scoped>
.btn-check {
    +.btn {
        letter-spacing: .2em;
        font-weight: 600;
    }
}
</style>