<template>
    <div>
        <Teleport to="body">
            <!-- Modal -->
            <div class="modal fade" id="capture-modal" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true"
                ref="capture">
                <div class="modal-dialog modal-dialog-centered">
                    <div class="modal-content">
                        <div class="modal-header">
                            <h1 class="modal-title lead fs-6" id="exampleModalLabel">Screenshot</h1>
                            <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                        </div>
                        <div class="modal-body">

                            <div class="d-flex justify-content-center align-items-center loader" v-if="isLoading">
                                <div class="spinner-border" role="status">
                                    <span class="visually-hidden">Loading...</span>
                                </div>
                            </div>

                            <h3 class="text-danger text-center fs-6 p-3" v-if="errorCapture">
                                Une erreur s'est produite lors de la capture !
                            </h3>

                            <img :src="currentImage" class="img-fluid" alt="" loading="lazy" v-else />

                        </div>
                        <div class="modal-footer">

                            <div class="d-flex">
                                <input class="form-control form-control-sm mx-3" id="filename" type="text"
                                    placeholder="Nom du fichier" aria-label=".form-control-sm name" v-model="fileName"
                                    required :disabled="isLoading || errorCapture">

                                <button type="button" class="btn btn-primary" :disabled="isLoading || errorCapture"
                                    @click="download">Telecharger</button>
                                <a :href="captureLink" :download="imgName" v-show="false" ref="downloadLink">Telecharger</a>
                            </div>

                        </div>
                    </div>
                </div>
            </div>

        </Teleport>

    </div>
</template>


<script setup>

import { ref, watch, computed, onMounted, onUnmounted } from "vue";
import { Modal } from "bootstrap";

import { useCaptureStore } from "@/stores/capture";
import { storeToRefs } from "pinia";

const caprureStore = useCaptureStore();
const { isLoading, isOpenModal, currentImage, captureLink, errorCapture } = storeToRefs(caprureStore);


const fileName = ref("");
const capture = ref(null);
const modal = ref(undefined);
const downloadLink = ref(null);

const imgName = computed(() => {
    return (fileName.value !== '') ? `${fileName.value}.jpg` : 'Screenshot.jpg';
});

onMounted(() => {
    modal.value = new Modal(capture.value);
    capture.value.addEventListener("hidden.bs.modal", () => {
        isOpenModal.value = false;
        fileName.value = "";
    })
});

onUnmounted(() => {
    modal.value.dispose();
});


watch(isOpenModal, () => {
    if (isOpenModal.value)
        modal.value.show();
});

const download = () => {
    downloadLink.value.click();
}


</script>


<style lang="scss" scoped>
.loader {
    width: 5em;
    min-height: 15em;
    margin: auto;

    .spinner-border {
        width: 3em;
        height: 3em;
    }
}
</style>