<template>
    <div class="loader w-100 text-center" v-if="isLoading">
        <div class="spinner-border" role="status">
            <span class="visually-hidden">Loading...</span>
        </div>
    </div>
    <img v-else :src="sourceImg" :srcset="srcSets" :sizes="sizes" class="img-fluid" alt="" loading="lazy">
</template>


<script setup>

import { useImage } from '@vueuse/core';
import { reactive, computed } from "vue";

const props = defineProps({
    src: {
        type: String,
        default: ""
    },
});


//const defaultImage = "https://via.placeholder.com/512x256.png/fff/ccc?text=Image not found";
const defaultImage = require("@/assets/images/lots/3d/appt2_0_bloc22.jpg");

const defaultBreakPoints = reactive({
    "small": 1000,
    "medium": 2000,
    "large": 3000,
    "x-large": 4000,
});

const sizes = computed(() => {
    let sizesAttr = "";
    Object.values(defaultBreakPoints).forEach(function (v) {
        sizesAttr += `(max-width: ${v}px) ${v}px, `;
    });
    return (srcSets.value !== "") ? sizesAttr : "";
});

const srcSets = computed(() => {
    const source = props.src;
    const splited = source.split('/');
    const name = splited[splited.length - 1];
    const baseString = splited.splice(0, splited.length - 1).join('/');

    let p = "";
    Object.entries(defaultBreakPoints).forEach(function ([k, v]) {
        const path = `${baseString}/${k}/${name}`;

        let set = getSrcsetPath(path);
        set = (set !== '') ? set + ` ${v}w, ` : '';
        p += set;
    });

    const smallBp = defaultBreakPoints.small;
    const smallPath = source;

    let smallSet = getSrcsetPath(smallPath);

    smallSet = (smallSet !== '') ? `${smallSet} ${smallBp}w, ` : '';

    const srcSet = `${smallSet} ${p}`;
    return (p !== "") ? srcSet : "";
});

const sourceImg = computed(() => {
    const source = props.src;
    return getImagePath(source);
});

const { isLoading } = useImage({ src: sourceImg.value });




function getImagePath(path) {
    let src = "";
    try {
        src = require(`@/${path}`);
    } catch (error) {
        src = defaultImage;
    }
    return src;
}

function getSrcsetPath(path) {
    let src = "";
    try {
        src = require(`@/${path}`);
    } catch (error) {
        src = "";
    }
    return src;
}


</script>

<style lang="scss" scoped>
.spinner-border {
    width: 3em;
    height: 3em;
}

img {
    width: auto !important;
    object-fit: contain;
    margin: auto;
}
</style>