import { Zoom } from "./zoom";
import { Drag } from "./drag";

class InputSystem {
    constructor(rect , naturalSizes , zoomOptions , dragOptions) {
        this.rect = rect;
        this.drag = new Drag(rect , dragOptions);
        this.zoom = new Zoom(rect , naturalSizes , zoomOptions);
        
        this.init();
    }

    _hasTouch() {
        return (('ontouchstart' in window));
    }

    init() {
        
        if(this._hasTouch()) {
            console.log("touch support");
            //touch events listeners
            this._touchListeners();
        }else{
            // mouse events
            this._mouseListeners();
            /**
             * zoom control events
             */
            this._pointerZoomEvent();
        }

        this.rect.addEventListener('wheel', (e) => {
            e.preventDefault();
            this.zoom._wheelZoom(e);
        });

    }

    _mouseListeners(){
        this.rect.onmousedown = e => {
            if(e.button === 0) this._mouseDown(e);            
        }

        this.rect.onmousemove = e => {
            this._mouseMove(e);
        }

        this.rect.onmouseup = (e) => {
            this._mouseUp(e);
        }
    }

    _touchListeners(){
        this.rect.addEventListener("touchstart" , (e) => {
            this._touchStart(e);
        });

        this.rect.addEventListener("touchmove" , (e) => {
            e.preventDefault();
            this._touchMove(e);
        } , { passive: false }); // without !passive event isn't preventing default behavior in ios safari 15+

        this.rect.addEventListener("touchend" , (e) => {
            this._touchEnd(e);
        });
    }

    
    _pointerZoomEvent(){
        
        //pointer events for zoom
        this.rect.addEventListener("pointerdown" , (e) => {
            if(e.button !== 0) {
                this.zoom.pointerEnd(e);
                return;
            }
            this.zoom.pointerStart(e);
        });

        this.rect.addEventListener("pointermove" , (e) => {
            e.preventDefault();
            this.zoom.pointerMove(e);
        });

        this.rect.addEventListener("pointerup" , (e) => {
            this.zoom.pointerEnd(e);
            
        });

        this.rect.addEventListener("pointercancel" , (e) => {
            this.zoom.pointerEnd(e);
           
        });

        this.rect.addEventListener("pointerout" , (e) => {
            this.zoom.pointerEnd(e);
        });

        this.rect.addEventListener("pointerleave" , (e) => {
            this.zoom.pointerEnd(e);
        });
    }


    


    //#region Mouse interactions
    _mouseDown(e){
        this.drag._startEvent(e.clientX);
    }
    _mouseMove(e){
        
        if(this.zoom.isZooming){
            this.drag.clear();
            return;
        } 
        
        this.drag.mouseMove(e);
    }
    _mouseUp(e){
        this.drag.mouseUp(e);
    }
    //#endregion



    //#region Touch interactions
    _touchStart(e) {
        const touches = e.changedTouches;
        this.zoom.touchStart(e);
        this.drag._startEvent(touches[0].clientX);
        
    }
    _touchMove(e) {
        this.zoom.touchMove(e);
        if(this.zoom.isZooming){
            this.drag.clear();
            return;
        } 
        this.drag.touchMove(e);
        
        
    }
    _touchEnd(e) {
        this.zoom.touchEnd(e);
        this.drag._endEvent();
    }
    //#endregion
}


export {InputSystem}