<template>
    <div class="stages-parent w-100">
        <div class="fs-6 text-center text-secondary mb-2">
            <span>Etages</span>
        </div>
        <div class="container-fluid">
            <div class="row justify-content-center">
                <div class="col-5 col-sm-2 col-md-3 position-relative p-0 m-1 text-center" v-for="index in stageCount"
                    :key="index">
                    <input type="checkbox" class="btn-check" :id="'stages-' + index" :value="index - 1"
                        v-model="checkedStages" autocomplete="off">
                    <label class="btn border-0 bg-white text-black text-center p-2" :for="'stages-' + index">

                        <svg xmlns="http://www.w3.org/2000/svg" width="1.2em" height="1.2em" fill="currentColor"
                            class="bi bi-layers-half" viewBox="0 0 16 16">
                            <path
                                d="M8.235 1.559a.5.5 0 0 0-.47 0l-7.5 4a.5.5 0 0 0 0 .882L3.188 8 .264 9.559a.5.5 0 0 0 0 .882l7.5 4a.5.5 0 0 0 .47 0l7.5-4a.5.5 0 0 0 0-.882L12.813 8l2.922-1.559a.5.5 0 0 0 0-.882l-7.5-4zM8 9.433 1.562 6 8 2.567 14.438 6 8 9.433z" />
                        </svg> {{ index - 1 }}

                    </label>
                </div>
            </div>
        </div>

    </div>
</template>


<script setup>


import { ref, watch, onMounted } from "vue";


const emits = defineEmits([
    "onStagesChanged"
]);

const props = defineProps({
    stageCount: Number,
    defaultStages: {
        type: Array,
        default() {
            return []
        }
    }
});

const checkedStages = ref(props.defaultStages);

function stageChanged() {
    emits("onStagesChanged", checkedStages.value);
}

watch(checkedStages, () => {
    stageChanged();
});

watch(() => props.defaultStages, () => {
    checkedStages.value = props.defaultStages;
    stageChanged();
});

onMounted(() => {
    stageChanged();
});

</script>



<style lang="scss" scoped>
.btn-check {
    +.btn {
        font-size: .9em;
        font-weight: normal;
        border-radius: .2em;
    }
}

.btn-check:checked {
    +.btn {
        filter: brightness(0.9);
        position: relative;

    }

}
</style>