<template>
    <div class="position-absolute popper" ref="popper" v-show="showPopover">
        <div class="card shadow border border-0">
            <div class="card-header p-0 overflow-hidden">
                <div class="color-type"></div>
                <div class="container-fluid">
                    <div class="row align-items-center bg-dark text-white py-2 text-uppercase text-center">
                        <div class="col-4 p-0">
                            <p class="p-0 m-0">Appt-{{ appt }}</p>
                        </div>
                        <div class="col-4 p-0">
                            <p class="p-0  m-0">Bloc-{{ bloc }}</p>
                        </div>
                        <div class="col-4 p-0">
                            <p class="p-0  m-0">Etage-{{ etage }}</p>
                        </div>
                    </div>
                </div>

            </div>
            <div class="card-body">
                <div class="container-fluid">

                    <div class="row align-items-center p-1">
                        <div class="col-4">
                            <span>Surface</span>
                        </div>
                        <div class="col-8">
                            <p>{{ currentRegion.surface }} M<sup>2</sup></p>
                        </div>
                    </div>

                    <div class="row align-items-center p-1">
                        <div class="col-4">
                            <span>Type</span>
                        </div>
                        <div class="col-8">
                            <p>{{ currentRegion.type }}</p>
                        </div>
                    </div>

                    <div class="row align-items-center p-1">
                        <div class="col-4">
                            <span>Code</span>
                        </div>
                        <div class="col-8">
                            <p>{{ currentRegion.code_ref }}</p>
                        </div>
                    </div>
                </div>


            </div>

            <div class="card-footer bg-transparent">
                <span>{{ currentRegion.statut }}</span>
                <span class="mx-1 text-success" v-if="currentRegion.statut === 'Disponible'">
                    <svg xmlns="http://www.w3.org/2000/svg" width="1.5em" height="1.5em" fill="currentColor"
                        class="bi bi-check-lg" viewBox="0 0 16 16">
                        <path
                            d="M12.736 3.97a.733.733 0 0 1 1.047 0c.286.289.29.756.01 1.05L7.88 12.01a.733.733 0 0 1-1.065.02L3.217 8.384a.757.757 0 0 1 0-1.06.733.733 0 0 1 1.047 0l3.052 3.093 5.4-6.425a.247.247 0 0 1 .02-.022Z" />
                    </svg>
                </span>

                <span class="mx-1 text-danger" v-else>
                    <svg xmlns="http://www.w3.org/2000/svg" width="1.6em" height="1.6em" fill="currentColor" class="bi bi-x"
                        viewBox="0 0 16 16">
                        <path
                            d="M4.646 4.646a.5.5 0 0 1 .708 0L8 7.293l2.646-2.647a.5.5 0 0 1 .708.708L8.707 8l2.647 2.646a.5.5 0 0 1-.708.708L8 8.707l-2.646 2.647a.5.5 0 0 1-.708-.708L7.293 8 4.646 5.354a.5.5 0 0 1 0-.708z" />
                    </svg>
                </span>
            </div>
        </div>
    </div>
</template>


<script setup>


import { ref, watch, onMounted, onUnmounted } from "vue";
import { createPopper } from '@popperjs/core';
import { useRegionsStore } from "@/stores/regions";
import { storeToRefs } from 'pinia';

const regionsStore = useRegionsStore();



const props = defineProps({

    showPopover: {
        type: Boolean,
        default: false
    },
    positions: {
        type: Object,

    }
});


const { currentColor, currentRegion, appt, bloc, etage } = storeToRefs(regionsStore);

let pInstance = null;
let virtualElement = null;
const popper = ref(null);


function initPopover() {
    virtualElement = {
        getBoundingClientRect: generateGetBoundingClientRect(),
    };
    pInstance = createPopper(virtualElement, popper.value, {
        placement: 'top',
        modifiers: [
            {
                name: 'offset',
                options: {
                    offset: ({ placement }) => {
                        if (placement === 'bottom') {
                            return [0, 30];
                        }

                        if (placement === 'top') {
                            return [0, 30];
                        }

                        return [];
                    },
                }
            },
        ]
    });
}

function updatePopover(x, y) {
    virtualElement.getBoundingClientRect = generateGetBoundingClientRect(x, y);
    pInstance.update();
}

function generateGetBoundingClientRect(x = 0, y = 0) {
    return () => ({
        width: 0,
        height: 0,
        top: y,
        right: x,
        bottom: y,
        left: x,
    });
}

watch(props.positions, () => {
    updatePopover(props.positions.x, props.positions.y);
});

onMounted(() => {
    initPopover();
});

onUnmounted(() => {
    pInstance.destroy();
});

</script>




