import { COLOR_HELPER} from "../utils/helpers";
import APP_DATA  from "@/assets/appData.json";


class Hotspots{

    /**
     * @param {Object} canvasGroup the app canvas object
     * @param {HTMLCanvasElement} canvasGroup.eventsCanvas - events capture
     * @param {HTMLCanvasElement} canvasGroup.hotspotsDraw - hotspots drawer
     * @param {HTMLCanvasElement} canvasGroup.hotspotsEffects - hotspots Effects
     * @param {HTMLCanvasElement} canvasGroup.animation - animation drawer
     * @param {HTMLCanvasElement} canvasGroup.tilesCanvas - tiles drawer
     * @param {HTMLCanvasElement} canvasGroup.hotspotsRef - hotspots references drawer
     * @param {Array<object>} hotspotRegions
     * @param {Object} regionsInfo
     */
    constructor(canvasGroup , hotspotRegions , regionsInfo)
    {
        this._canvasGroup = canvasGroup;
        
        this._hotspotRegions = hotspotRegions;

        this.regionsInfo = regionsInfo;

        this.imgW, this.imgH , this.level = 0 , this.frame = 0;

        this.htDrawCtx , this.htRefCtx;

        /**
         * array containing all hotspots,
         * ordered by level and frame,
         * ukey is the unique color to be pointed when hovering a polygon,
         * pointing to unique color instead of looping trough all filtredRegions by row,
         * the column ... is moore efficient
         * e.g this.filtredRegions[row][column][uKey] = {}
         */
        this.filtredRegions = [[[]]];
        
        /**
         * the unique color set, we use Set to ensure non duplicate value
         */
        this._uColorSet;
        

        /**
         * object of filters to be applied when drawing hotspots,
         * e.g this.filterOptions.surface = {min : Number , max:Number}
         */
        this.filterOptions = {};

        this._hsRefTimer;
        this._delayRefDraw = 500;

        this._lastHovered = undefined;

        this.init();
    }

    init(){
        
        this.htDrawcanvas = this._canvasGroup.hotspotsDraw;
        this.htEffectCanvas = this._canvasGroup.hotspotsEffects;
        this.htRefCanvas = this._canvasGroup.hotspotsRef;
        this.htEvtCanvas = this._canvasGroup.eventsCanvas;

        this.htDrawCtx = this.htDrawcanvas.getContext("2d");
        this.htEffectCtx = this.htEffectCanvas.getContext("2d");
        this.htRefCtx = this.htRefCanvas.getContext("2d" , {willReadFrequently: true }); // For more info about the [willReadFrequently] : https://html.spec.whatwg.org/multipage/canvas.html#dom-canvasrenderingcontext2dsettings-willreadfrequently
        this.htEvtCtx = this.htRefCanvas.getContext("2d");
        

        this.typesColors = APP_DATA.hotspots.color_HS;
        
        
        this.setupFromXml();

        //this.setup3DProjection();
        this.drawHts();
        this.hSEvents();
        
    }
    

    /**
     * setup the filtred regions,
     * not that this method can change depending on the data comming from the config file ,
     * for now we just test...
     */
    setupFromXml() {
        this._uColorSet = new Set();

        
        this._hotspotRegions.forEach(ht => {

            // name of the polygon
            const name = ht.$.id;
            
            //get the background color and transform to hex code
            const type = this.regionsInfo[name].type;
            //const polyColor = ht.$.backgroundcolor.replace("0x" , "#");
            const polyColor = this.typesColors[type];
            
            // get the opacity
            //const polyAlpha = ht.$.backgroundalpha;
            const polyAlpha = 0.4;

            

            // for every hotspots polygon
            ht.polygon.forEach(p => {

                //get the polygon
                const poly = p.$;
                
                // get the level
                const row = parseInt(poly.state);

                //get the frame
                const column = parseInt(poly.column);

                // get the formated positions
                const fPos = this._formatPositions(poly.vertices);

                // get the unique color to be indexed with the polygon
                let uniqColor = COLOR_HELPER.getUniqColor(this._uColorSet);
                this._uColorSet.add(uniqColor);
                uniqColor = COLOR_HELPER.hexToRgb(uniqColor);
                
                const polyObj = {
                    name : name,
                    polygon : fPos,
                    colorRef : uniqColor,
                    fillColor : polyColor,
                    alpha : polyAlpha,

                }

                // create unique key based on the unique color 
                const uKey = COLOR_HELPER.getColorString(uniqColor);

                /**
                 * prepare the multi dimensional array, 
                 * row for every level, and column for every frame
                 */
                if(typeof(this.filtredRegions[row]) === "undefined") {
                    this.filtredRegions[row] = [];
                }
                if(typeof(this.filtredRegions[row][column]) === "undefined") {
                    this.filtredRegions[row][column] = [];
                }

                //add the poly indexed by the unique key
                this.filtredRegions[row][column][uKey] = polyObj;
            })
                        

        })

        //clear garbage
        this._hotspotRegions.length = 0;

    }


    drawHts(level , frame , imgW , imgH , offsetX , offsetY) {
        this.level = level;
        this.frame = frame;
        this.imgW = imgW;
        this.imgH = imgH;
        this.offsetX = offsetX;
        this.offsetY = offsetY;
        this._drawHts();
    }


    _drawHts() {
        window.clearTimeout(this._hsRefTimer);
        this._clearCtx(this.htRefCtx);
        this._clearCtx(this.htEffectCtx);
        
        this.drawHotspots();

        this._hsRefTimer = window.setTimeout( () => {
            this.drawHotspotsRef();
        } , this._delayRefDraw);
    }

    drawHotspots() {

        this._clearCtx(this.htDrawCtx);
        const region = this._getCurrentRegion();

        if(region == undefined) return;
        
        Object.values(region).forEach((poly) => {
            
            if(this.validate(poly.name)) {
                const positions = poly.polygon;
                const c = COLOR_HELPER.getColorFill(COLOR_HELPER.hexToRgb(poly.fillColor) , poly.alpha);
                this._drawPolygon(this.htDrawCtx , positions , c , undefined , 1);
            }
            
        })

    }


    /**
     * hospots reference, this is the main handler of all interactions with the hotspots,
     */
    drawHotspotsRef() {
        const region = this._getCurrentRegion();
        if(region == undefined) return

        this._clearCtx(this.htRefCtx);

        Object.values(region).forEach((poly) => {
            
            if(this.validate(poly.name)) {
                const positions = poly.polygon;
                const fill = COLOR_HELPER.getColorFill(poly.colorRef , 1);
    
                requestAnimationFrame(() => this._drawPolygon(this.htRefCtx , positions , fill , undefined));
            }

        })

    }

    _drawPolygon(ctx , positions , fillStyle , strokeStyle , lineWidth = 2) {

        ctx.strokeStyle = strokeStyle;
        ctx.lineWidth = 1.0;
        ctx.fillStyle = fillStyle;

        ctx.lineWidth = lineWidth;

        ctx.beginPath();
        for (let i = 0; i < positions.length; i++) {
            const points = positions[i];

            const margX = Math.round( (ctx.canvas.width - this.imgW) * 0.5 );
            const margY = Math.round( (ctx.canvas.height - this.imgH) * 0.5 );

            //console.log(margX , margY);

            const x = ((points.x * this.imgW) + margX) - this.offsetX;
            const y = (points.y * this.imgH + margY) - this.offsetY;

            ctx.lineTo(x ,y);
        }

        ctx.fill();
        
        ctx.closePath();
        if(strokeStyle) ctx.stroke();
    }


    /**
     * 
     * @param {String} regionName 
     * @returns {Boolean} 
     */
    validate(regionName) {
        const infoPoly = this.regionsInfo[regionName];
        if(infoPoly !== undefined) {
            const surface = parseInt( infoPoly.surface , 10 );
            const type = infoPoly.type;
            const filterOpt = this.filterOptions.surface;
            const filterTypes = this.filterOptions.types;

            if(filterOpt === undefined || filterTypes === undefined) return false;

            if(surface < filterOpt.minSurface || surface > filterOpt.maxSurface) {
                return false;
            }

            if(!filterTypes.includes(type)) return false;
            
            return true;
        }
        return false;
    }

    /**
     * 
     * @param {Object} surface the surface object to filter by
     */
    setSurfaceFilter(surface) {
        this.filterOptions.surface = {
            minSurface : surface.min,
            maxSurface : surface.max,
        }
        this._updateHotspots();
    }

    setTypesFilter(types) {
        this.filterOptions.types = types
        this._updateHotspots();
    }


    _updateHotspots() {
        this._drawHts();
    }
    
    /**
     * 
     * @param {String} pos polygon positions
     * @returns {Array} formated positions
     */
    _formatPositions(pos){
        let positions = [];
        const reg = pos;
        const polys = reg.split('|');
        for (let i = 0; i < polys.length; i++) {
            const coor = polys[i].split(";");
            
            for (let j = 0; j < coor.length; j++) {
                const obj = coor[j].split(",");
                positions.push({
                    x : obj[0],
                    y : obj[1],
                })
            }
            }
        return positions;
    }
    
    



    hSEvents() {

        this.htEvtCanvas.addEventListener("mousemove" , (e) => {
            this.mouseMoveEvent(e);
        });

        
        if(window.PointerEvent) {

            this.htEvtCanvas.addEventListener("pointerdown" , (e) => {
                this._startEvent(e);
            });
            this.htEvtCanvas.addEventListener("pointerup" , (e) => {
                if(this._canClick()) {
                    this.clickEvent(e);
                }
                
            });

        }else{

            this.htEvtCanvas.addEventListener("mousedown" , (e) => {
                this._startEvent(e);
            });
            this.htEvtCanvas.addEventListener("mouseup" , (e) => {
                if(this._canClick()) {
                    this.clickEvent(e);
                }
            });

            this.htEvtCanvas.addEventListener("touchstart" , (e) => {
                this._startEvent(e);
            });
    
            this.htEvtCanvas.addEventListener("touchend" , (e) => {
                if(this._canClick()) {
                    this.touchEvent(e);
                }
            });
        }
        
    }

    mouseMoveEvent(e) {
        const insideData = this._getHotspotData({x : e.clientX , y : e.clientY});
        if( insideData === undefined ){
            this._regionOutState();
            return;
        }
        this._regionHoverState(insideData , {x : e.clientX , y : e.clientY});
        
    }


    clickEvent(e){
        const insideData = this._getHotspotData({x : e.clientX , y : e.clientY});
        if( insideData !== undefined ){
            this._regionClickState(insideData , {x : e.clientX , y : e.clientY});
        }
    }
    touchEvent(e){
        const touch = e.changedTouches[0];
        const insideData = this._getHotspotData({x : touch.clientX , y : touch.clientY});
        if( insideData !== undefined ){
            this._regionClickState(insideData , {x : touch.clientX , y : touch.clientY});
        }
    }

    

    _startEvent(){
        this.timeStart = Date.now();
    }

    _canClick() {
        const t = Date.now();

        const elapsed = t - this.timeStart;

        if(elapsed < 300) {
            return true;
        }

        return false;
    }

    _regionHoverState(color , mousePos) {

        const regionData = this._getSelectedHotspot(color);
        if(!regionData) {
            return;
        }

        
        const region = regionData.region;
        if(this._lastHovered === undefined) {
            this._setHsEffect(region);
        }else{
            
            if(this._lastHovered.name !== region.name){
                this._clearCtx(this.htEffectCtx);
                this._setHsEffect(region);
            }
        }
        

        //add hover inline css style
        this.htEvtCanvas.style.cursor = "pointer";

        /**
         * dispatch the OnHSHover global event on the window,
         * sending mousePos and the hovered polygon
         */

        window.dispatchEvent(new CustomEvent("OnHSHover", {
            bubbles: false,
            detail : {
                mousePos : mousePos,
                region : regionData.region
            }
        }));
    }

    _regionOutState(){

        if(this._lastHovered !== undefined){
            this._clearCtx(this.htEffectCtx);
            this._lastHovered = undefined;
        } 
        

        this.htEvtCanvas.style.cursor = "default";
        window.dispatchEvent(new CustomEvent("OnHSHoverOut", {
            bubbles: false,
        }));
    }

    _regionClickState(color , mousePos) {

        this._regionOutState();
        const regionData = this._getSelectedHotspot(color);
        
        if(regionData === undefined) {
            return;
        }

        /**
         * dispatch the OnHSClick global event on the window,
         * sending mousePos and the clicked polygon info
         */
        const regionName = regionData.region.name;
        window.dispatchEvent(new CustomEvent("OnHSClick", {
            bubbles: false,
            detail : {
                mousePos : mousePos,
                region : this.regionsInfo[regionName]
            }
        }));

    }

    _setHsEffect(region){
        this._drawPolygon(this.htEffectCtx , region.polygon , 'transparent' , '#FFFFFF' , 2);
        this._lastHovered = region;
    }


    _getHotspotData(pos) {
        const bounding = this.htDrawcanvas.getBoundingClientRect();
        const x = Math.round(pos.x - bounding.left);
        const y = Math.round(pos.y - bounding.top);

        // Get image pixel coordinate
        const pixel = this.htRefCtx.getImageData(x, y, 1, 1);
        const data = pixel.data;

        // if color is not matching any poly 
        if( data[0] === 0 && data[1] === 0 && data[2] === 0 ){
            
            return undefined;
        }
        
        return data;
    }

    _getSelectedHotspot(color){

        //get the current region from filtered regions based on frame and level
        const region = this._getCurrentRegion();
        if(region === undefined) return undefined;

        //get the RGB color
        const c = {red : color[0] , green:color[1] , blue :color[2] }

        //get the key string from the color
        const cString = COLOR_HELPER.getColorString(c);

        //point to the polyon with the given key
        const poly = region[cString];
        if(poly === undefined) {
            return undefined;
        }

        return {region : poly };
    }





    

    /**
     * return the current region based on current level and frame
     * @returns {Array<Object>} current region
     */
    _getCurrentRegion(){

        if(typeof(this.filtredRegions[this.level]) === "undefined") {
            this._clearCtx(this.htRefCtx);
            return undefined;
        }

        if(typeof(this.filtredRegions[this.level][this.frame]) === "undefined") {
            this._clearCtx(this.htRefCtx);
            return undefined;
        }

        return this.filtredRegions[this.level][this.frame];
    }

    _clearCtx(ctx) {
        ctx.clearRect(0, 0, ctx.canvas.width, ctx.canvas.height);
    }
        
}


export { Hotspots}