class TilesController {

    /**
     * @param {Object} options
     * some params needed in options
     * @param {Object} options.breakpoints_sizes - Breakpoints sizes , ex. keys : { small , medium , large}
     * @param {Number} options.cropSize - the tile size of the splitted image (e.g 512 for 512x512)
     * @param {Number} options.maxCache - the max cache of tiles
     *  
     */

    constructor (options) {

        this.options = options;
        
        if(typeof options.breakpoints_sizes === "undefined" && typeof options.breakpoints_sizes !== "object") {
            alert("breakpoints not valid")
            throw new Error("breakpoints not valid");
        }
        

        /**
         * Original images sizes
         */

        this.naturalSizes = options.breakpoints_sizes;

        /**
         * the preloaded size by default (this is loaded when the page load)
         */

        this.preloadedBP = this.naturalSizes.preloaded;


        this.cropSize = options.cropSize;
        this.maxCache = options.maxCache;


        /**
         * the current natural image width ,
         * when loading tiles we must know the original width of the current breakpoint
         * we must calculate height based on width with the right aspect ratio
         */
        
        this._currentNaturalWidth = null;

        this._animationIndex = 0;
        this._currentFrame = 0;
        this._currentBreakPoint;
        this._currentScale;

        /** 
         * the cache array , contain lazy-loaded images width additional info
         * */

        this._cachedFrames = new Array();

        this._useCache = true;

        this._timeOutLoad;
        this._firstFetch = true;

        this.init();
    }

    init() {

        this._setLimitCache();
        // creating sorted breakpoints sizes from min to max
        this.breakPoints = Object.entries(this.naturalSizes)
            .sort(([,a],[,b]) => a-b)
            .reduce((r, [k, v]) => ({ ...r, [k]: v }), {});

       
    }


    _setLimitCache(){

        if (/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent)) {

            if(!performance.memory) {
                
                this.maxCache = 15;
                return;
            }

            if (performance.memory.jsHeapSizeLimit < 500000000) {
               
                this.maxCache = 15;
                return;
            }
        }
    }

    /**
     * 
     * @param {Number} animationIndex - the animation index 
     * @param {Number} frameIndex - the current frame index 
     * @param {Number} currentWidth - the current image width 
     * @param {Number} scale - the current zoom/scale
     * @returns resolve with loaded tiles || reject
     */
    fetchTiles(animationIndex , frameIndex , currentWidth , scale) {
        
        this._setCurrentBP(currentWidth);
        this._setCurrentNaturalSize(this._currentBreakPoint);
        
        if(currentWidth <= this.preloadedBP) return this._loadTiles(true);

        this._animationIndex = animationIndex;
        this._currentFrame = frameIndex;
        this._currentScale = scale;

        if(this._firstFetch) {
            //first fetch
            console.log("first fetch");
            this._firstFetch = false;
            return this._loadTiles();
        }

        if(this._useCache) {
            const preloaded = this._cachedFrames.find((v) => {
                if(
                    v.animationIndex === this._animationIndex &&
                    v.frame === this._currentFrame && 
                    v.breakPoint === this._currentBreakPoint
                ) 
                {
                    return v;
                }
            });
    
            if(typeof preloaded !== "undefined") {
                return this._loadExistingTiles(preloaded);
            }
        }
        

        return this._loadTiles();
        
    }



    /**
     * load tiles from cache
     * @param {Object} preloaded the cached tiles object if found
     * @returns the cached canvas containing tiles of the current frame 
     */
    _loadExistingTiles(preloaded){
        
        return new Promise((resolve , reject) => {
            if(
                preloaded.animationIndex === this._animationIndex && 
                preloaded.frame === this._currentFrame && 
                preloaded.breakPoint === this._currentBreakPoint
            )
            {
                resolve(preloaded.canvas);
            }
            else{
                reject();
            }
        });
    }

    /**
     * load tiles of current frame
     * @param {boolean} error this is helpful for rejecting the promise if frame is changed while loading tiles
     * @returns the loaded canvas containing tiles of the current frame 
     */
    _loadTiles(error = false) {
        window.clearTimeout(this._timeOutLoad);
        const p = new Promise((resolve , reject ) => {
            if(error) {
                reject();
            }else{
                this._timeOutLoad = window.setTimeout( () => {
                    
                    const animationIndex = this._animationIndex;
                    const frameIndex = this._currentFrame;
                    const breakpoint = this._currentBreakPoint;
                    const scaleFactor = this._currentScale;

                    const folder = this._getTilesFolder(animationIndex , frameIndex , breakpoint);

                    this._createAndLoadTiles(folder,  (WSCanvas) => {
                        
                        if(this._useCache) {
                            const tilesInfo = {
                                animationIndex : animationIndex,
                                frame : frameIndex,
                                breakPoint : breakpoint,
                                canvas : WSCanvas
                            };
        
                            this._addToCache(tilesInfo);
                        }
                        
    
                        if(!this._canLoadImg(animationIndex , frameIndex , breakpoint , scaleFactor)) {
                            reject();
                        }
                        resolve(WSCanvas);
                    });
                } , 100);
                
            }
            
        });
        return p;
    }

    /**
     * add loaded tiles if not already exist into the cache 
     * @param {Object} cacheInfo info about the cached canvas 
     */
    _addToCache(cacheInfo) {
        
        if(this._cachedFrames.includes(cacheInfo)) return;

        this._checkCache();
        this._cachedFrames.push(cacheInfo);
    }
    

    /**
     * load tiles method , this is multi-thread by using the Promise all
     * @param {String} folder the tiles folder
     * @param {Function} onFinish calllback when finish loading tiles
     */
    _createAndLoadTiles(folder , onFinish) {

        const natWidth = this._currentNaturalWidth;
        const natHeight = this._calculateWithAspectRatio(natWidth);

        let canvas = document.createElement('canvas');
        let ctx = canvas.getContext("2d");

        canvas.width = natWidth;
        canvas.height = natHeight;
        
        const cropSize = this.cropSize; // the crop rect size
        
        const partsX = Math.floor(natWidth / cropSize);
        const partsY = Math.floor(natHeight / cropSize);

        const moduloX = natWidth % cropSize;
        const moduloY = natHeight % cropSize;

        const lengthX = moduloX != 0 ? partsX+1 : partsX;
        const lengthY = moduloY != 0 ? partsY+1 : partsY;

        let allPromises = new Array();
        
        for(let y= 0 ; y < lengthY ; y++) {
            for(let x = 0 ; x < lengthX ; x++ ) {

                const p = new Promise((resolve , reject) => {
                    let sx = x*cropSize ,
                    sy = y*cropSize;
                    
                    const index = lengthX * y + x;
                    const src = `${folder}p-${index}.jpg` ;
                    const path = require("@/" + src);
    
                    let img = new Image();
                    img.decoding = "async";
                    
                    img.onload = () => {
                        ctx.drawImage(img, sx, sy);
                        img = null;
                        resolve();
                        return;
                    };
                    img.onerror = () => {
                        img = null;
                        reject();
                    };
                    img.src = path;
                });

                allPromises.push(p);

            }
        }

        Promise.all(allPromises).then(() => {
            onFinish(canvas);
            allPromises = null;
        })
    }

    /* releaseCanvas(canvas) {
        canvas.width = 1;
        canvas.height = 1;
        const ctx = canvas.getContext('2d');
        ctx && ctx.clearRect(0, 0, 1, 1);
    } */


    /**
     * if loaded tiles is the same after the fetch
     * @param {Number} animationIndex - the animation index when tiles loaded
     * @param {Number} frame - the frame when tiles loaded
     * @param {Number} breakPoint 
     * @param {Number} scaleFactor 
     * @returns 
     */
    _canLoadImg(animationIndex , frame , breakPoint , scaleFactor) {

        if(
            animationIndex !== this._animationIndex || 
            frame !== this._currentFrame || 
            breakPoint !== this._currentBreakPoint || 
            scaleFactor !== this._currentScale
        )
        {
            return false;
        }
        return true;
    }

    
    /**
     * 
     * @param {Context} ctx 
     * @param {HTMLCanvasElement} img 
     * @param {Number} sx - x source coordinate
     * @param {Number} sy - y source coordinate
     */
    _draw(ctx , img , sx , sy) {
        ctx.clearRect(sx, sy, ctx.canvas.width, ctx.canvas.height);
        ctx.drawImage(img, sx, sy);
    }


    /**
     * return the folder path depending on given breakpoint
     * @param {Number} animationIndex 
     * @param {Number} frameIndex 
     * @param {Number} breakpoint 
     * @returns  tiles folder
     */
    _getTilesFolder(animationIndex , frameIndex , breakpoint ){

        let tilesRegPath = this.options.imagesFolder + this.options.tilesFolder;
        
        for(const [key , bp] of Object.entries(this.breakPoints)) {
            if(breakpoint === bp) {
                const keyPath = this.options.breakpoints_folder[key];
                tilesRegPath = tilesRegPath
                    .replace(/%row/g , animationIndex)
                    .replace("%bp" , keyPath)
                    .replace("%col" , frameIndex);
                break;
            }
        }
        return tilesRegPath;
    }


    /**
     * set the current original size based on the given breakpoint
     * @param {Number} breakpoint 
     */
    _setCurrentNaturalSize(breakpoint) {

        for(const [key , bp] of Object.entries(this.breakPoints)) {
            if(bp === breakpoint) {
                this._currentNaturalWidth = this.naturalSizes[key];
                break;
            }
        }
    }

    /**
     * set current breakpoint based on given width
     * @param {Number} width 
     */
    _setCurrentBP(width){

        for(const [key=key , bp] of Object.entries(this.breakPoints)) {
            if(width <= bp) {
                this._currentBreakPoint = bp;
                break;
            }
            this._currentBreakPoint = bp;
        }
    }

    /**
     * check if cache is out of max cache , if so then we delete the first element of the cache array
     */
    _checkCache(){
        if(this._cachedFrames.length > this.maxCache) {
            
            let removed = this._cachedFrames.shift();
            removed.canvas = null;
            removed = null;
        }
    }


    /**
     * calculate the height of image based on width preserving the aspect ratio
     * @param {Number} width 
     * @returns the calculated height
     */
    _calculateWithAspectRatio(width) {
        const aspect = this.options.maxWidth / this.options.maxHeight;
        const height = width / aspect;

        return height;
    }


    
}


export {TilesController}