class Resizer {

    constructor (canvasObj) {

        this.canvasObj = canvasObj;
        this.init();
    }

    init(){
        this.resize();
    }

    resize(){

        Object.values(this.canvasObj).forEach((rect) => {
            rect.width = document.documentElement.clientWidth;
            rect.height = document.documentElement.clientHeight;
        });

    }
}

export {Resizer}