<template>
    <div class="container-fluid card-foot mb-3">

        <div class="row align-items-center">
            <div class="col">
                <span class="text-secondary dispo">Disponibilité</span>
            </div>
            <div class="col">
                <span class=" text-success" v-if="infoRef.statut == 'Disponible'">
                    <svg xmlns="http://www.w3.org/2000/svg" width="1.5em" height="1.5em" fill="currentColor"
                        class="bi bi-check-lg" viewBox="0 0 16 16">
                        <path
                            d="M12.736 3.97a.733.733 0 0 1 1.047 0c.286.289.29.756.01 1.05L7.88 12.01a.733.733 0 0 1-1.065.02L3.217 8.384a.757.757 0 0 1 0-1.06.733.733 0 0 1 1.047 0l3.052 3.093 5.4-6.425a.247.247 0 0 1 .02-.022Z" />
                    </svg>
                </span>

                <span class="text-danger" v-else>
                    <svg xmlns="http://www.w3.org/2000/svg" width="1.5em" height="1.5em" fill="currentColor" class="bi bi-x"
                        viewBox="0 0 16 16">
                        <path
                            d="M4.646 4.646a.5.5 0 0 1 .708 0L8 7.293l2.646-2.647a.5.5 0 0 1 .708.708L8.707 8l2.647 2.646a.5.5 0 0 1-.708.708L8 8.707l-2.646 2.647a.5.5 0 0 1-.708-.708L7.293 8 4.646 5.354a.5.5 0 0 1 0-.708z" />
                    </svg>
                </span>
            </div>
        </div>


        <div class="form-parent d-flex flex-column justify-content-center align-items-center" v-if="isAdmin">
            <div class="me-3 h-100" v-if="showLoader">
                <div class="spinner-border text-type-color text-center" role="status">
                    <span class="visually-hidden">Loading...</span>
                </div>
            </div>
            <div class="col-12 my-2" v-if="!showLoader">
                <form action="" @submit.prevent="">
                    <div class="form-check">
                        <input class="form-check-input" type="radio" value="Disponible" :name="infoRef.statut"
                            :id="infoRef.nom + '0'" v-model="status">
                        <label class="form-check-label" :for="infoRef.nom + '0'">
                            Disponible
                        </label>
                    </div>
                    <div class="form-check">
                        <input class="form-check-input" type="radio" value="Indisponible" :name="infoRef.statut"
                            :id="infoRef.nom + '1'" v-model="status">
                        <label class="form-check-label" :for="infoRef.nom + '1'">
                            Indisponible
                        </label>
                    </div>
                </form>
            </div>

            <div class="form-footer w-100 col d-flex justify-content-between align-items-center" v-if="!showLoader">

                <button class="btn btn-success btn-sm" @click="sendEdit(infoRef.nom)">Edit</button>


                <div class="msg text-center w-75 p-2">
                    <div class="alert m-0 p-2" :class="alertStyle">
                        {{ alertMsg }}
                    </div>
                </div>

            </div>
        </div>

    </div>
</template>



<script setup>

import { ref, watch, computed } from "vue";

import { useRegionsStore } from "@/stores/regions";
import { storeToRefs } from 'pinia';

const regionsStore = useRegionsStore();
const { regions, isError } = storeToRefs(regionsStore);


const props = defineProps({
    cardInfo: Object,
    colorType: String,
    isAdmin: {
        type: Boolean,
        default: false
    }
});

const infoRef = ref(props.cardInfo);

const status = ref(infoRef.value.statut);
let lastStatus = status.value;

const showLoader = ref(false);
const alertMsg = ref(infoRef.value.statut);
const isReqError = ref(false);
const alertStyle = computed(() => (
    {
        'alert-success': infoRef.value.statut === "Disponible" && !isReqError.value,
        'alert-danger': infoRef.value.statut !== "Disponible" && !isReqError.value,
        'alert-warning': isReqError.value,
    }
));


function sendEdit(id) {

    if (lastStatus === status.value) return;
    showLoader.value = true;
    alertMsg.value = 'Loading';
    regionsStore.editStatus(id, status.value).then(msg => {
        if (isError.value) {
            showLoader.value = false;
            isReqError.value = true;
            alertMsg.value = msg;
            window.setTimeout(() => {
                const stat = infoRef.value.statut;
                isReqError.value = false;
                alertMsg.value = stat;
                lastStatus = alertMsg.value = status.value = stat;
            }, 1000);
            return;
        }

        infoRef.value = regions.value[id];
        lastStatus = status.value = infoRef.value.statut;
        isReqError.value = false;
        showLoader.value = false;
        alertMsg.value = msg;

        window.setTimeout(() => {
            alertMsg.value = infoRef.value.statut;
        }, 1000);
    });
}



watch(() => props.cardInfo, () => {
    infoRef.value = props.cardInfo;
    lastStatus = status.value = infoRef.value.statut;
});


</script>



<style lang="scss" scoped>
.text-type-color {
    color: v-bind(colorType);
}

.dispo {
    font-size: .9em;
}

.form-parent {
    min-height: 8em;
}

input,
label {
    cursor: pointer;
}

label {
    font-size: .8em;
    font-weight: bold;
    color: #6a6a6a;
}

.form-footer {
    height: 4em;

    .msg {
        font-size: .9em;
    }
}
</style>